import React, { useEffect, useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { signup } from '../actions/auth';
import axios from 'axios';

import login_main_image from '../img/Icon/login.jpg'
import fb from '../img/Icon/fb-icon.png'
import google from '../img/Icon/google-icon.png'
import logo from "../img/logo.png"
// import '../build/css/demo.css'
// import '../build/css/intlTelInput.css'
// import '../build/js/intlTelInput'
// import '../build/js/utils'
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'

const Signup = ({ signup, isAuthenticated,isError }) => {
const[error_signup, setErrorSignup] = useState(false)
const [redirection, setRedirect] = useState(false)
const [accountCreated, setAccountCreated] = useState(false);
const [process, setProcess] = useState(false)
const [email_error , setEmailError]= useState(false)
const [terms, setTerms] = useState(false)
const [terms_error, setTermsError] = useState(false)
const [submit_attempt ,setSubmitAttempt] = useState(false)
const [cell , setCell] = useState('')

useEffect(()=>{

   
    if (isError) {
      setErrorSignup(true)
      // alert(isError['password'], isError['email'])
      if(isError['password']){
        // alert(isError['password'])
        if(isError['password'].length>0)
        {
          console.log('the length is', isError['password'].length,isError['password'])

          setRePassError(true)
          setErrorMessage(isError['password'][0])
          // console.log('there is an error',isError['password'])

        }
          
        
      }
      if (isError['email']){
        // alert(isError['email'])

        if(isError['email'].length>0)
        {
          console.log('the length is', isError['email'].length,isError['email'])

        setEmailError(true)
        setErrorMessage(isError['email'][0])
        // console.log('there is an error',isError['email'])

        }

      }
      

  }
  else if(isError === undefined ){
    setErrorSignup(false)
    }
  else if(isError === false && process === true){
    setAccountCreated(true)
    // alert('The activation link has been sent at your email.')

  }

  },[isError])
    const [error, setError] = useState(false)
    const [re_pass_error, setRePassError] = useState(false)
    const[error_message, setErrorMessage] = useState('')
    const [formData, setFormData] = useState({
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        re_password: '',
        cell_number:''
    });

    const { first_name, last_name, email, password, re_password, cell_number} = formData;

    const onChange = e => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
        setRePassError(false)
        setError(false)
        setEmailError(false)
        setTermsError(false)

    }

    const onSubmit = e => {
        e.preventDefault();
        setSubmitAttempt(true)
        if(!terms){
          setTermsError(true)
        }
        else if (password === re_password) {
          cell_number = cell
            signup(first_name, last_name, email, password, re_password, cell_number);
            setProcess(true)
            // setAccountCreated(true);
          // if(!isError){

          //   console.log('errorrr',isError)
          // }
            
        }
        else{
            setRePassError(true)
            setErrorMessage('Passwords do not match.')
        }
    };
    const termsChange = e=>{
      setTermsError(false)
      setTerms(!terms)
    }
    const continueWithGoogle = async () => {
      try {
          const res = await axios.get(`https://ubuntu.anms.pk/auth/o/google-oauth2/?redirect_uri=https://ubuntu.anms.pk/google`)

          window.location.replace(res.data.authorization_url);
      } catch (err) {
console.log(err)
      }
  };

  const continueWithFacebook = async () => {
      try {
          const res = await axios.get(`https://ubuntu.anms.pk/auth/o/facebook/?redirect_uri=https://ubuntu.anms.pk/facebook`)

          window.location.replace(res.data.authorization_url);
      } catch (err) {
console.log(err)
      }
  };

    if (isAuthenticated) {
        return <Redirect to='/' />
    }
    if (accountCreated)  {
        return <Redirect to='/login' />
    }

    return (
        <>
        {/* <div className='container mt-5'>
            <h1>Sign Up</h1>
            <p>Create your Account</p>
            <form onSubmit={e => onSubmit(e)}>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='text'
                        placeholder='First Name*'
                        name='first_name'
                        value={first_name}
                        onChange={e => onChange(e)}
                        required
                    />
                </div>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='text'
                        placeholder='Last Name*'
                        name='last_name'
                        value={last_name}
                        onChange={e => onChange(e)}
                        required
                    />
                </div>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='email'
                        placeholder='Email*'
                        name='email'
                        value={email}
                        onChange={e => onChange(e)}
                        required
                    />
                </div>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='password'
                        placeholder='Password*'
                        name='password'
                        value={password}
                        onChange={e => onChange(e)}
                        minLength='6'
                        required
                    />
                </div>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='password'
                        placeholder='Confirm Password*'
                        name='re_password'
                        value={re_password}
                        onChange={e => onChange(e)}
                        minLength='6'
                        required
                    />
                </div>
                <button className='btn btn-primary' type='submit'>Register</button>
            </form>
            <button className='btn btn-danger mt-3' onClick={continueWithGoogle}>
                Continue With Google
            </button>
            <br />
            <button className='btn btn-primary mt-3' onClick={continueWithFacebook}>
                Continue With Facebook
            </button>
            <p className='mt-3'>
                Already have an account? <Link to='/login'>Sign In</Link>
            </p>
        </div>
        */}
        
        <div class="container h-100" style={{marginTop:'70px'}}> 
        {redirection?<Redirect to='/'/>:null}
      <div class="d-flex justify-content-center align-items-center h-100">
        <div class="login-panel">
          <div class="row">
            <div class="col-xs-12 col-sm-6">
              <div class="login-banner" 
              style={{
                backgroundImage: `url(${login_main_image})`, 
                backgroundRepeat: "no-repeat",
                height: "100%",
                borderRadius: "20px 0 0 20px",
                background: "cover",
                backgroundPosition: "right"
                    }}   >
                
              </div>
            </div>
            <div class="col-xs-12 col-sm-6">
             <div class="login-form">
                <div class="login-logo">
                  <img onClick={e=>setRedirect(true)} src={logo} style={{cursor:'pointer'}} alt="Logo Here" />
                </div>
                <form onSubmit={e => onSubmit(e)}>
                  <div class="input-field">
                    <label>Full Name</label>
                    <input
                        className='form-controls'
                        type='text'
                        placeholder='John Smith'
                        name='first_name'
                        value={first_name}
                        onChange={e => onChange(e)}
                        required
                    />
                  </div>
                  <div class="input-field">
                    <label>Cell Number</label>
                    {/* <input
                        className='form-controls'
                        type='text'
                        placeholder='+10000000000'
                        name='cell_number'
                        value={cell_number}
                        onChange={e => onChange(e)}
                        
                    /> */}
                  <PhoneInput
                  className='outline_none'
                  name="cell_number"
                    inputExtraProps={{
                      name: "cell_number",
                      required: false,
                      autoFocus: false,
                      placeholder:'#####'

                    }}
                   

                    country={'us'}
                    value={cell}
                    onChange={e => setCell(e)}
                  />
                  </div>
                  <div class="input-field">
                    <label>Email Address</label>
                    <input
                        style={email_error && submit_attempt?{border:'2px solid red'}:null}
                        className='form-controls'
                        // type='email'
                        placeholder='john@xyz.com'
                        name='email'
                        value={email}
                        onChange={e => onChange(e)}
                        // required
                    />                 
                  </div>
                  {email_error && submit_attempt?<div>
                        <p style={{color:'red',fontSize:'10px'}}>
                          {
                            error_message==='Enter a valid email address.'?<>Enter a valid email address.</>
                            :
                            <>
                            User email already exists. Please <a style={{cursor:'pointer', color:'#042f54'}} onClick={e=>setAccountCreated(true)}> Login </a> to continue.

                            </>

                          }
                        </p>
                    </div>:null}
                  <div class="input-field">
                    <label>Password</label>
                    <input
                    style={re_pass_error && submit_attempt?{border:'2px solid red'}:null}
                        className='form-controls'
                        type='password'
                        placeholder='*********'
                        name='password'
                        value={password}
                        onChange={e => onChange(e)}
                        // minLength='6'
                        // required
                    />                  </div>
                  <div class="input-field">
                    <label>Confirm Password</label>
                    <input
                        style={re_pass_error && submit_attempt?{border:'2px solid red'}:null}

                        className='form-controls'
                        type='password'
                        placeholder='*********'
                        name='re_password'
                        value={re_password}
                        onChange={e => onChange(e)}
                        // minLength='6'
                        required
                    />                  
                    </div>
                    {re_pass_error && submit_attempt?<div>
                        <p style={{color:'red',fontSize:'10px'}}>{error_message}</p>
                    </div>:null}
                  <div class="checkbox custom-checkbox pull-left">
                    <label>
                      <input type="checkbox" onClick={e => termsChange()} />
                      <span class="checkbox-decorator"><span class="check"></span>
                      </span> 
                      <span class="font-14" style={{display:'inline-block', marginLeft:'5px'}} >
                        I accept the
                        <a href={`https://ubuntu.anms.pk/about`} 
                        target="_blank" 
                        style={{
                       cursor:'pointer',
                       color:'#042f54',
                       textDecoration: "underline"

                       
                       }} >  terms of use 
                       </a>  and 
                       <a href={`https://ubuntu.anms.pk/about`} 
                       target="_blank"
                       style={{
                        cursor:'pointer',
                        color:'#042f54',
                        textDecoration: "underline"
                      }} 
                       > privacy policy
                       </a>
                       </span>
                    </label>
                  </div>
                  
                  {terms_error && submit_attempt?<div>
                        <p style={{color:'red',fontSize:'10px',display:'inline-block'}}>
                        Please accept Terms and Conditions to continue
                           </p>
                    </div>:null}
                  
                  <div class="form-group">
                    <button class="btn btn-info btn-lg btn-block login-btn ">Sign Up</button>
                  </div>
                  <div class="header-title">
                    <h2><span class="tile-login">Signup With</span></h2>
                  </div>
                  <div class="social-login-btns">
                    <ul>
                      <li>
                        <a >
                        <img src={fb} style={{cursor:'pointer'}} onClick={continueWithFacebook} alt="Facebook" />
                        </a>
                      </li>
                      <li>
                        <a >
                        <img src={google} style={{cursor:'pointer'}} onClick={continueWithGoogle} alt="Google" />
                        </a>
                      </li>
                    </ul>
                  </div>
                </form> 
             </div>  
            </div>
          </div>  
        </div>
      </div>
    </div>

    <div className="form-group">
      <p style={{textAlign:'center', marginTop:'20px'}}>
        Already have an account?
        <a href="" 
          style={{
           textDecoration:'underline' ,
           cursor:'pointer',
            color:'#042f54'}} 
            onClick={e=>setAccountCreated(true)}>
             <span> </span> Login
        </a>
      </p>
     </div>
        </>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    isError: state.auth.isError

});

export default connect(mapStateToProps, { signup })(Signup);
