import React, { useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password } from '../actions/auth';
import login_main_image from '../img/Icon/login.jpg'
import logo from '../img/logo.png'
const ResetPassword = ({ reset_password ,is_reset_password}) => {
    useEffect(()=>{
        console.log('there is my reset password',is_reset_password)
    },[is_reset_password])
    const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        email: ''
    });
    const[message, setMessage] = useState('')
    const[redirect, setRedirect] = useState(false)
    const { email } = formData;

    const onChange = e =>{
        setFormData({ ...formData, [e.target.name]: e.target.value });
        setMessage('')
    }

    const onSubmit = e => {
        e.preventDefault();

        reset_password(email);

        setMessage('The instruction to reset the password has been emailed to you.')
        setFormData({email:''})
        // setRequestSent(true);
    };

    if (requestSent) {
        return <Redirect to='/' />
    }

    return (
        <>
        {redirect?<Redirect to='/' />:null}
            
        <div class="container h-100" style={{marginTop:'100px'}}>
            <div class="d-flex justify-content-center align-items-center h-100">
                <div class="login-panel">
                <div class="row">
                    <div class="col-xs-12 col-sm-6">
                        <div class="login-banner" style={{
                                backgroundImage: `url(${login_main_image})`, 
                                backgroundRepeat: "no-repeat",
                                height: "100%",
                                borderRadius: "20px 0 0 20px",
                                background: "cover",
                                backgroundPosition: "right"
                            }}>
                        </div>
                    </div>
                    <div class="col-xs-12 col-sm-6">
                        <div class="login-form">
                            <div class="login-logo">
                            <img  src={logo} style={{cursor:'pointer'}} onClick={e=>setRedirect(true)} alt="Logo Here" />

                            </div>
                            <form onSubmit={e => onSubmit(e)}>
                                <strong style={{color:'#042f54'}}> Forgot Password</strong>
                                    <div class="input-field">
                                        <label>Email Address</label>
                                        <input
                                            className='form-controls'
                                            type='email'
                                            placeholder='Email'
                                            name='email'
                                            value={email}
                                            onChange={e => onChange(e)}
                                            required
                                        />
                                    </div>
                                    <div>
                                       {message!==''?
                                     <p style={{fontSize:'10px',textAlign:'center',color:'black'}}> 
                                        The instructions to reset the password have been emailed to you.                                    </p>  
                                    :null
                                    
                                    }
                                       
                                    </div>
                                    {/* <div>
                                        <p style={{fontSize:'10px',color:'green'}}>Make sure you provided the registered email address</p>
                                    </div> */}
                                <div class="form-group mt-3">
                                    <button type='submit' class="btn btn-info btn-lg btn-block login-btn ">Forgot Password</button>
                                </div>
                            </form> 
                        </div>  
                    </div>
                </div>  
            </div>
        </div>
        </div>
        </>
    );
};

const mapStateToProps = state => ({
    is_reset_password:state.auth.is_reset_password
});

export default connect(mapStateToProps, { reset_password })(ResetPassword);
// export default connect(mapStateToProps, { login,removeError })(Login);
